"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { createClient } from "@/utils/supabase/client";
import { completeOnboarding } from "./actions";
import { Loader } from "lucide-react";
import { User } from "@supabase/supabase-js";

const steps = [
  {
    title: "Welcome to Arti Notes",
    description:
      "Let's get you set up with your personalized note-taking experience.",
  },
  {
    title: "Choose Your Note Style",
    description: "Select how you prefer to organize your notes.",
  },
  {
    title: "Set Up Your Workspace",
    description: "Customize your workspace to match your workflow.",
  },
];

export default function OnboardingPage() {
  const router = useRouter();
  const [currentStep, setCurrentStep] = useState(0);
  const [loading, setLoading] = useState(false);
  const [isCheckingOnboarding, setIsCheckingOnboarding] = useState(true);
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const supabase = createClient();
        const {
          data: { user },
        } = await supabase.auth.getUser();
        setUser(user);
        setIsLoading(false);
      } catch (error) {
        console.error("Error checking auth:", error);
        setIsLoading(false);
      }
    };

    checkAuth();
  }, []);

  useEffect(() => {
    if (!user || isLoading) return;

    const checkOnboarding = async () => {
      try {
        const { data, error } = await createClient()
          .from("user_onboarding")
          .select("completed_at")
          .eq("user_id", user.id)
          .single();

        if (error) throw error;

        if (data?.completed_at) {
          router.push("/dashboard");
        } else {
          setIsCheckingOnboarding(false);
        }
      } catch (error) {
        console.error("Error checking onboarding:", error);
        setIsCheckingOnboarding(false);
      }
    };

    checkOnboarding();
  }, [user, isLoading, router]);

  const handleSubmit = async () => {
    setLoading(true);
    try {
      if (!user) {
        throw new Error("No user found");
      }

      await completeOnboarding(user.id);
      router.push("/dashboard");
    } catch (error) {
      console.error("Error completing onboarding:", error);
      setLoading(false);
    }
  };

  if (isLoading || isCheckingOnboarding) {
    return (
      <div className="flex h-screen items-center justify-center">
        <Loader className="h-8 w-8 animate-spin" />
      </div>
    );
  }

  return (
    <div className="flex min-h-screen flex-col items-center justify-center p-4">
      <div className="w-full max-w-md space-y-8">
        <div className="text-center">
          <h1 className="text-3xl font-bold">{steps[currentStep].title}</h1>
          <p className="mt-2 text-gray-600">{steps[currentStep].description}</p>
        </div>

        <div className="mt-8">
          <div className="flex justify-center space-x-2">
            {steps.map((_, index) => (
              <div
                key={index}
                className={`h-2 w-2 rounded-full ${
                  index === currentStep ? "bg-blue-600" : "bg-gray-300"
                }`}
              />
            ))}
          </div>
        </div>

        <div className="mt-8">
          {currentStep === steps.length - 1 ? (
            <button
              onClick={handleSubmit}
              disabled={loading}
              className="w-full rounded-md bg-blue-600 px-4 py-2 text-white hover:bg-blue-700 disabled:opacity-50"
            >
              {loading ? "Completing..." : "Complete Setup"}
            </button>
          ) : (
            <button
              onClick={() => setCurrentStep(currentStep + 1)}
              className="w-full rounded-md bg-blue-600 px-4 py-2 text-white hover:bg-blue-700"
            >
              Next
            </button>
          )}
        </div>
      </div>
    </div>
  );
}
